@import url(https://fonts.googleapis.com/css2?family=Bree+Serif&family=Gluten:wght@300&family=Shippori+Antique&family=Ubuntu:wght@700&family=Yuji+Mai&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.navbar {
  background-color: white !important;
}

.navbar-brand {
  font-size: 3rem !important;
  font-family: Yuji Mai !important;
  font-weight: bold;
}

.navbar a {
  font-size: 1.5rem;
  color: brown !important;
  font-family: Yuji Mai;
}

.navbar a:hover {
  color: black !important;
}

.menu_active {
  font-weight: bold;
  border-bottom: 1px solid rgb(48, 30, 25);
}

#header {
  width: 100%;
  height: 60vh;
  margin-top: 50px;
}

#header h1 {
  margin: 0 0 10px 0;
  font-family: Ubuntu;
}

#header .brand-name {
  color: rgb(17, 58, 105);
}

#header h2 {
  font-family: Gluten;
  color: rgb(31, 185, 31);
}

#header .btn-get-started {
  text-decoration: none;
  font-weight: 500;
  font-size: 16px;
  letter-spacing: 1px;
  display: inline-block;
  padding: 10px 30px;
  color: rgb(212, 126, 28);
  border-radius: 50px;
  border: 2px solid peru;
  transition: 0.5s;
}

#header .btn-get-started:hover {
  background: yellowgreen;
  color: wheat;
}

.header-img {
  text-align: right;
}

#header .animated {
  animation: up-down 2s ease-in-out infinite alternate-reverse both;
}

@keyframes up-down {
  0% {
    transform: translateY(10px);
  }
  100% {
    transform: translateY(-10px);
  }
}

.heading {
  font-family: Ubuntu;
  color: goldenrod;
}

